import React from 'react';
import "./CoinRow.css";

const CoinRow = ({
    name,
    current_price,
    image,
    symbol
  }) => {
    return (
      <div className='coin'>
        <div className='logo'>
          <img src={image} alt='logo' />
        </div>
        <div className='name'>
          <h2>{name}</h2>
        </div>
        <div className='symbol'>
            <h2>{symbol.toUpperCase()}</h2>
        </div>
        <div className='price'>
          <h2>${current_price.toLocaleString()}</h2>
        </div>
      </div>
    );
  };

export default CoinRow
