import { useState, useEffect } from "react";
import"./Coins.css";
import axios from "axios";
import CoinRow from "./CoinRow";

const Coins = () => {
  const [coins, setCoins] = useState([]);
  const [page, setPage] = useState(1);
  const setData = (page) => {
    if (page < 1 || page > 34) setPage(1);
    axios
      .get(
        `https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=${page}&sparkline=false`
      )
      .then((res) => {
        setCoins(res.data);
      });
  };
  useEffect(() => {
    setData(page);
  }, [page]);


  const List = coins.map((coin) => <CoinRow {...coin} key={coin.id} />);
  return (
    <div>
      <div className='page'>
        <i className='fas fa-caret-square-left fa-2x'  onClick={() => setPage(page - 1)}></i>
        <h2>Page - {page}</h2>
        <i className='fas fa-caret-square-right fa-2x'  onClick={() => setPage(page + 1)}></i>
      </div>
      <main className='main'>{List}</main>
    </div>
  );
};
export default Coins;